// ==========================================
// RISIVO CRM - Theme API Routes
// ==========================================

import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin } from '@/lib/supabase/server'
import { SubAccountTheme, ThemePreset, ThemeApiResponse, ThemePresetsResponse } from '@/lib/theme/types'
import { RISIVO_DEFAULT_THEME } from '@/lib/theme/defaults'

/**
 * GET /api/v1/themes
 * Get theme for a sub-account or list all presets
 * 
 * Query params:
 * - subAccountId: Get theme for specific sub-account
 * - presets: If true, return list of all theme presets
 */
export async function GET(request: NextRequest) {
  try {
    const searchParams = request.nextUrl.searchParams
    const subAccountId = searchParams.get('subAccountId')
    const getPresets = searchParams.get('presets') === 'true'
    
    // If requesting presets - return empty array if table doesn't exist
    if (getPresets) {
      try {
        const { data: presets, error } = await supabaseAdmin
          .from('ThemePreset')
          .select('*')
          .eq('isActive', true)
          .order('displayOrder', { ascending: true })
        
        if (error) {
          // Table might not exist, return empty presets
          console.warn('ThemePreset query error:', error.message)
          return NextResponse.json<ThemePresetsResponse>({
            success: true,
            data: [],
          })
        }
        
        return NextResponse.json<ThemePresetsResponse>({
          success: true,
          data: presets || [],
        })
      } catch (e) {
        // Return empty presets on any error
        return NextResponse.json<ThemePresetsResponse>({
          success: true,
          data: [],
        })
      }
    }
    
    // If requesting sub-account theme
    if (!subAccountId) {
      // Return default theme if no subAccountId
      return NextResponse.json<ThemeApiResponse>({
        success: true,
        data: RISIVO_DEFAULT_THEME,
        message: 'Returned default RISIVO theme',
      })
    }
    
    // Try to get custom theme first
    const { data: customTheme, error: themeError } = await supabaseAdmin
      .from('SubAccountTheme')
      .select('*')
      .eq('subAccountId', subAccountId)
      .single()
    
    // If custom theme exists, merge with defaults and return
    if (customTheme && !themeError) {
      const mergedTheme: SubAccountTheme = {
        ...RISIVO_DEFAULT_THEME,
        ...customTheme,
        subAccountId,
      }
      
      return NextResponse.json<ThemeApiResponse>({
        success: true,
        data: mergedTheme,
      })
    }
    
    // Check if sub-account has basic color settings
    const { data: subAccount, error: saError } = await supabaseAdmin
      .from('SubAccount')
      .select('id, name, logo, primaryColor, secondaryColor')
      .eq('id', subAccountId)
      .single()
    
    if (saError || !subAccount) {
      // Return default theme with subAccountId
      return NextResponse.json<ThemeApiResponse>({
        success: true,
        data: { ...RISIVO_DEFAULT_THEME, subAccountId },
        message: 'Sub-account not found, using default theme',
      })
    }
    
    // Build theme from sub-account basic settings
    const theme: SubAccountTheme = {
      ...RISIVO_DEFAULT_THEME,
      subAccountId,
      logoUrl: subAccount.logo || RISIVO_DEFAULT_THEME.logoUrl,
      primaryColor: subAccount.primaryColor || RISIVO_DEFAULT_THEME.primaryColor,
      sidebarBg: subAccount.primaryColor || RISIVO_DEFAULT_THEME.sidebarBg,
      secondaryColor: subAccount.secondaryColor || RISIVO_DEFAULT_THEME.secondaryColor,
      buttonPrimaryBg: subAccount.secondaryColor || RISIVO_DEFAULT_THEME.buttonPrimaryBg,
    }
    
    return NextResponse.json<ThemeApiResponse>({
      success: true,
      data: theme,
    })
    
  } catch (error) {
    console.error('GET /api/v1/themes error:', error)
    return NextResponse.json<ThemeApiResponse>({
      success: false,
      error: error instanceof Error ? error.message : 'Failed to get theme',
    }, { status: 500 })
  }
}

/**
 * POST /api/v1/themes
 * Create or update theme for a sub-account
 * 
 * Body:
 * - subAccountId: Required
 * - theme: Partial<SubAccountTheme>
 * - applyPreset?: string (preset name to apply)
 */
export async function POST(request: NextRequest) {
  try {
    const body = await request.json()
    const { subAccountId, theme, applyPreset } = body
    
    if (!subAccountId) {
      return NextResponse.json<ThemeApiResponse>({
        success: false,
        error: 'subAccountId is required',
      }, { status: 400 })
    }
    
    let themeData: Partial<SubAccountTheme> = theme || {}
    
    // If applying a preset, get preset data
    if (applyPreset) {
      const { data: preset, error: presetError } = await supabaseAdmin
        .from('ThemePreset')
        .select('*')
        .eq('name', applyPreset)
        .single()
      
      if (presetError || !preset) {
        return NextResponse.json<ThemeApiResponse>({
          success: false,
          error: `Preset '${applyPreset}' not found`,
        }, { status: 404 })
      }
      
      // Merge preset with any custom overrides
      themeData = {
        primaryColor: preset.primaryColor,
        primaryHover: preset.primaryHover || RISIVO_DEFAULT_THEME.primaryHover,
        secondaryColor: preset.secondaryColor,
        secondaryHover: preset.secondaryHover || RISIVO_DEFAULT_THEME.secondaryHover,
        backgroundColor: preset.backgroundColor || RISIVO_DEFAULT_THEME.backgroundColor,
        surfaceColor: preset.surfaceColor || RISIVO_DEFAULT_THEME.surfaceColor,
        textPrimary: preset.textPrimary || RISIVO_DEFAULT_THEME.textPrimary,
        textSecondary: preset.textSecondary || RISIVO_DEFAULT_THEME.textSecondary,
        successColor: preset.successColor || RISIVO_DEFAULT_THEME.successColor,
        warningColor: preset.warningColor || RISIVO_DEFAULT_THEME.warningColor,
        errorColor: preset.errorColor || RISIVO_DEFAULT_THEME.errorColor,
        infoColor: preset.infoColor || RISIVO_DEFAULT_THEME.infoColor,
        // Sidebar inherits from primary
        sidebarBg: preset.primaryColor,
        // Button inherits from secondary
        buttonPrimaryBg: preset.secondaryColor,
        colorSource: 'preset',
        presetName: preset.name,
        ...theme, // Custom overrides
      }
    }
    
    // Prepare data for upsert
    const upsertData = {
      subAccountId,
      ...themeData,
      updatedAt: new Date().toISOString(),
    }
    
    // Check if theme already exists
    const { data: existing } = await supabaseAdmin
      .from('SubAccountTheme')
      .select('id')
      .eq('subAccountId', subAccountId)
      .single()
    
    let result
    
    if (existing) {
      // Update existing
      const { data, error } = await supabaseAdmin
        .from('SubAccountTheme')
        .update(upsertData)
        .eq('subAccountId', subAccountId)
        .select()
        .single()
      
      if (error) throw error
      result = data
    } else {
      // Insert new
      const { data, error } = await supabaseAdmin
        .from('SubAccountTheme')
        .insert(upsertData)
        .select()
        .single()
      
      if (error) throw error
      result = data
    }
    
    // Also update SubAccount basic colors for backwards compatibility
    await supabaseAdmin
      .from('SubAccount')
      .update({
        primaryColor: themeData.primaryColor,
        secondaryColor: themeData.secondaryColor,
      })
      .eq('id', subAccountId)
    
    // Merge with defaults for complete response
    const fullTheme: SubAccountTheme = {
      ...RISIVO_DEFAULT_THEME,
      ...result,
      subAccountId,
    }
    
    return NextResponse.json<ThemeApiResponse>({
      success: true,
      data: fullTheme,
      message: applyPreset ? `Applied preset '${applyPreset}'` : 'Theme saved successfully',
    })
    
  } catch (error) {
    console.error('POST /api/v1/themes error:', error)
    return NextResponse.json<ThemeApiResponse>({
      success: false,
      error: error instanceof Error ? error.message : 'Failed to save theme',
    }, { status: 500 })
  }
}

/**
 * DELETE /api/v1/themes
 * Reset theme to default for a sub-account
 * 
 * Query params:
 * - subAccountId: Required
 */
export async function DELETE(request: NextRequest) {
  try {
    const searchParams = request.nextUrl.searchParams
    const subAccountId = searchParams.get('subAccountId')
    
    if (!subAccountId) {
      return NextResponse.json<ThemeApiResponse>({
        success: false,
        error: 'subAccountId is required',
      }, { status: 400 })
    }
    
    // Delete custom theme
    const { error } = await supabaseAdmin
      .from('SubAccountTheme')
      .delete()
      .eq('subAccountId', subAccountId)
    
    if (error) throw error
    
    // Reset SubAccount colors to default
    await supabaseAdmin
      .from('SubAccount')
      .update({
        primaryColor: RISIVO_DEFAULT_THEME.primaryColor,
        secondaryColor: RISIVO_DEFAULT_THEME.secondaryColor,
      })
      .eq('id', subAccountId)
    
    return NextResponse.json<ThemeApiResponse>({
      success: true,
      data: { ...RISIVO_DEFAULT_THEME, subAccountId },
      message: 'Theme reset to default',
    })
    
  } catch (error) {
    console.error('DELETE /api/v1/themes error:', error)
    return NextResponse.json<ThemeApiResponse>({
      success: false,
      error: error instanceof Error ? error.message : 'Failed to reset theme',
    }, { status: 500 })
  }
}
